<template>
  <div class="practice">
    <el-row :gutter="20">
      <el-col :span="8">
        <el-input v-model="queryData.memo" placeholder="阶段名称"></el-input>
      </el-col>
      <el-col :span="8">
        <el-input v-model="queryData.name" placeholder="练习题目"></el-input>
      </el-col>
      <el-col :span="8">
        <el-button @click="sendPractice" type="primary">发布练习</el-button>
      </el-col>
    </el-row>
    <div style="margin: 10px 0;">
      <span class="demonstration">选择日期查看练习列表：</span>
      <el-date-picker
        type="date"
        v-model="timeDate"
        @change="getTaskList"
        placeholder="选择日期">
      </el-date-picker>
    </div>
    <div class="loading" v-loading="isLoading">
      <el-table v-if="studentsList.length" :data="studentsList" stripe border>
      <el-table-column fixed width="80" label="姓名" prop="name"></el-table-column>
      <el-table-column v-slot="{ row }" v-for="(item, i) in studentsList[0].practiceList" :key="item.id" :label="item.workname">
        <el-tag :type="row.practiceList[i].isComplete === 2 ? 'success' : 'danger'">
          {{ row.practiceList[i].isComplete === 2 ? '已完成' : '未完成' }}
        </el-tag>
      </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
import { publishTask, getTaskList } from '../../api/task.js'
export default {
  data () {
    return {
      queryData: {
        createdOn: new Date(),
        memo: 'vue',
        name: '测试',
        type: 1
      },
      timeDate: new Date(),
      studentsList: [],
      isLoading: false
    }
  },

  created () {
    this.getTaskList()
  },

  methods: {
    // 发布练习
    async sendPractice () {
      if (this.queryData.name.trim() === '') return this.$message('请填写练习阶段标题')
      if (this.queryData.memo.trim() === '') return this.$message('请填写练习内容')
      const { data: res } = await publishTask(this.queryData)
      if (res.code !== 0) return this.$message('发布失败')
      this.$message.success('发布成功')
      this.queryData.name = ''
      this.queryData.memo = ''
      this.getTaskList()
    },
    // 获取练习
    async getTaskList () {
      this.isLoading = true
      const { data: res } = await getTaskList({
        time: this.timeDate,
        type: 1
      })
      if (res.code !== 0) return this.$message('获取数据失败')
      this.studentsList = res.data
      this.isLoading = false
    }
  }
}
</script>

<style scoped lang='less'>
.practice {
  width: 95%;
  margin:15px auto;
  .loading {
    width: 100%;
    display: flex;
    align-items: center;
    min-height: 200px;
  }
  .el-table {
    margin-top: 10px;
  }
}
</style>
